import React from 'react'
import './style.css'
import {connect} from 'react-redux'
import { bindActionCreators } from 'redux'

const percent33 = 0.33;  
const percent67 = 0.67;

class CalculaSorosGale extends React.Component {
    copiaValor = (text) => {
        var textArea = document.createElement("textarea");
        textArea.value = text;
        document.body.appendChild(textArea);
        textArea.select();
        try {
            var successful = document.execCommand('copy');
            var msg = successful ? 'successful' : 'unsuccessful';
            console.log('Copying text command was ' + msg);
        } catch (err) {
            console.log('Oops, unable to copy');
            window.prompt("Copie para área de transferência: Ctrl+C e tecle Enter", text);
        }
        document.body.removeChild(textArea);
    }
    retorno(){
        let temp = parseFloat(this.props.newOrdem) * parseFloat(this.props.newPayout/100);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno(){
        let temp = (parseFloat( this.ordemA() ) * (this.props.newPayout/100) ) + parseFloat(this.props.newOrdem);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno2(){
        let temp = (parseFloat(this.ordemA2())*(this.props.newPayout/100))+parseFloat(this.retorno());
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno3(){
        let temp = (parseFloat(this.ordemA3())*(this.props.newPayout/100))+parseFloat(this.retorno2());
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno4(){
        let temp = (parseFloat(this.ordemA4())*(this.props.newPayout/100))+parseFloat(this.retorno3());
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno5(){
        let temp = (parseFloat(this.ordemA5())*(this.props.newPayout/100))+parseFloat(this.retorno4());
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }   
    ordemA(){
        let temp = parseFloat(this.props.newOrdem) * parseFloat(percent33);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemA2(){
        let temp = parseFloat(this.retorno()) * parseFloat(percent33);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemA3(){
        let temp = parseFloat(this.retorno2()) * parseFloat(percent33);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemA4(){
        let temp = parseFloat(this.retorno3()) * parseFloat(percent33);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemA5(){
        let temp = parseFloat(this.retorno4()) * parseFloat(percent33);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemB(){
        let temp = parseFloat(this.props.newOrdem) * parseFloat(percent67);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemB2(){
        let temp = parseFloat(this.retorno()) * parseFloat(percent67);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemB3(){
        let temp = parseFloat(this.retorno2()) * parseFloat(percent67);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemB4(){
        let temp = parseFloat(this.retorno3()) * parseFloat(percent67);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordemB5(){
        let temp = parseFloat(this.retorno4()) * parseFloat(percent67);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }   
    render(){
            return (
                <div class="tableResultado">
                  <table class="tableResultado">
                        <tr>
                            <td>
                                
                            </td>
                            <td class="tituloTabelaLinha">
                                <p>1° Ordem</p>
                            </td>
                            <td class="tituloTabelaLinha">
                                <p>2° Ordem</p>
                            </td>
                            <td class="tituloTabelaLinha">
                                <p>Retorno</p>
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                            <p>1° Nível</p>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordemA() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.ordemA() )} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordemB() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.ordemB() )} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.retorno() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.retorno() )} />
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                            <p>2° Nível</p>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordemA2() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.ordemA2() )} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordemB2() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.ordemB2() )} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.retorno2() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.retorno2() )} />
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                            <p>3° Nível</p>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordemA3() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.ordemA3() )} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordemB3() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.ordemB3() )} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.retorno3() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.retorno3() )} />
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                            <p>4° Nível</p>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordemA4() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.ordemA4() )} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordemB4() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.ordemB4() )} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.retorno4() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.retorno4() )} />
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                            <p>5° Nível</p>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordemA5() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.ordemA5() )} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordemB5() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.ordemB5() )} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.retorno5() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.retorno5() )} />
                                </div>
                            </td>
                        </tr>
                    </table>

                   
                </div>
            )
        }
    }

const mapStateToProps = state => ({
    newOrdem:  state.CalculosReducers.newOrdem,
    newPayout:  state.CalculosReducers.newPayout,
})
const mapDispatchToProps = dispatch => bindActionCreators({
}, dispatch);
export default connect(mapStateToProps, 
    mapDispatchToProps
)(CalculaSorosGale); 