import React from 'react'
import  './style.css'
import {connect} from 'react-redux'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom"
import {
    appCarregando
} from '../../redux/actions'
import { bindActionCreators } from 'redux'
import CenaSoros from './soros/CenaSoros'
import CenaSorosGale from './sorosGale/CenaSorosGale'
import CenaMartingale from './martingale/CenaMartingale'

class Principal extends React.Component {
    render(){
        const { newNome } = this.props;
        return (            
        <div class="tabelaCalculos">   
            <Router>
                <div>
                    {
                        newNome === ''
                        ? <h3>Seja bem vindo, selecione seu gerenciamento:</h3>
                        : <h3>Olá { newNome }, selecione seu gerenciamento:</h3>
                    }
                    <div>
                        <table class="menuCalculo">
                            <tr>
                                <td><a><Link exact to="/">SOROS</Link></a></td>
                                <td><a><Link exact to="/sorosGale">SOROSGALE</Link></a></td>
                                <td><a><Link exact to="/martingale">MARTINGALE</Link></a></td>
                            </tr>
                        </table>
                    </div>
                    <Switch>
                        <Route exact path="/">
                            <CenaSoros />
                        </Route>
                        <Route path="/martingale">
                            <Martingale />
                        </Route>
                        <Route path="/sorosGale">
                            <SorosGale />
                        </Route>
                    </Switch>
                </div>
            </Router>
        </div>
        );
    }
  }

const Martingale = () =>{
    return (
        <div>
            <CenaMartingale />
        </div>
    );
}

const SorosGale = () =>{
    return(
        <div>
            <CenaSorosGale />
        </div>
    );
}

const mapStateToProps = state => ({
    newNome: state.CalculosReducers.newNome,
    newEmail: state.CalculosReducers.newEmail,
    redirect: state.CalculosReducers.redirect,
})
const mapDispatchToProps = dispatch => bindActionCreators({
    appCarregando
}, dispatch);
export default connect(mapStateToProps, 
    mapDispatchToProps
)(Principal); 