import React from 'react';
import './style.css'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { Redirect } from 'react-router-dom'
import  logo  from '../../img/logoC.png'
import {
    appSair
} from '../../redux/actions'

class Logoff extends React.Component {    
    state = {
        loading: false,
    }
    _sair = () => {
        const { appSair } = this.props;
        appSair()
        setTimeout( () => {
            this.setState({ loading: true })
        }, 2000 )        
    }
    render(){
        const { loading } = this.state;
        return(
            <div class="baseFormulario">
                <table>                        
                    <tr><td>
                            <img src={logo} />
                    </td></tr>
                    <tr><td>
                    <h2>Obrigado pela preferência!</h2>
                    <input
                        type="submit" 
                        value="Sair" 
                        onClick={this._sair}
                            />
                    { 
                        loading
                        ? <Redirect to="/VerificaAuth"></Redirect> 
                        : null
                    }
                </td></tr>
                </table>
            </div>
        );
    }
}
const mapStateToProps = state => ({
    redirect: state.CalculosReducers.redirect,
})
const mapDispatchToProps = dispatch => bindActionCreators({
    appSair
}, dispatch);
export default connect(mapStateToProps, 
    mapDispatchToProps
)(Logoff); 