export const MODIFICA_STOPLOSS = 'MODIFICA_STOPLOSS';
export const modificaStopLoss = (stopLoss) => {
    return{
        type:'MODIFICA_STOPLOSS',
        payload: stopLoss
    }
}
export const MODIFICA_RETORNO = 'MODIFICA_RETORNO';
export const modificaRetorno = (retorno) => {
    return{
        type:'MODIFICA_RETORNO',
        payload: retorno
    }
}
export const ATUALIZA_ORDEM = "ATUALIZA_ORDEM";
export const atualizaOrdem = value => ({
    type: ATUALIZA_ORDEM,
    newOrdem: value,
});
export const ATUALIZA_PAYOUT = "ATUALIZA_PAYOUT";
export const atualizaPayout = value => ({
    type: ATUALIZA_PAYOUT,
    newPayout: value
});
export const ATUALIZA_STOPLOSS = "ATUALIZA_STOPLOSS";
export const atualizaStoploss = value => ({
    type: ATUALIZA_STOPLOSS,
    newStoploss: value
});
