import React from 'react'
import './style.css'
import {connect} from 'react-redux'
import logo from '../../img/logo.png'
import { Link } from 'react-router-dom'
import { bindActionCreators } from 'redux'
import {
    appSair
} from '../../redux/actions'

class Header extends React.Component {
    sairApp = async () => {
        const { appSair } = this.props;
        await appSair()
    }
    render(){
        return(
            <div class="topo">
                <table class="header">
                    <tr><td>
                            <img src={logo} />
                        </td>
                        <td>
                            <h3>Calculadora Pro Trader Investing</h3>
                    </td></tr>
                </table>
                <table class="menuPrincipal">
                    <tr>
                        <td><a><Link to="/Main">Inicio</Link></a></td>
                        <td><a><Link to="/Principal">Calculadora</Link></a></td>
                        <td><a><Link to="/Historico">Histórico</Link></a></td>
                        <td><a><Link to="/Main">Desempenho (EM BREVE)</Link></a></td>
                        <td><a><Link to="/Logoff">Sair</Link></a>
                        </td>
                    </tr>
                </table>
            </div>
        );
    }
}
const mapStateToProps = state => ({
    
})
const mapDispatchToProps = dispatch => bindActionCreators({
    appSair
}, dispatch);
export default connect(mapStateToProps, 
    mapDispatchToProps
)(Header); 